import Experience from '../Experience.js'
import { FontLoader } from 'three/examples/jsm/loaders/fontloader.js';
import { texts } from '../../../static/texts/Texts.js';
import EventEmitter from '../Utils/EventEmitter.js';
import { Text } from 'troika-three-text';
import { textStr } from '../../script.js';

const INITIAL_POS_Y = -36;

export default class TextManager extends EventEmitter
{
    constructor()
    {
        super()

        this.experience = new Experience()
        this.scene = this.experience.scene
        this.resources = this.experience.resources
        this.time = this.experience.time
        this.debug = this.experience.debug
        this.fontLoader = new FontLoader();
        this.speed = 0.001;
        this.scrollSens = 0.8;
        this.isRunning = true;

        this.autoScroll = true;
        this.autoScrollSlider = document.querySelector('#scroll-toggle');
        this.btnRestart = document.querySelector('.restart'); 

        // Debug
        if(this.debug.active)
        {
            this.debugFolder = this.debug.ui.addFolder('Text')
        }

        this.setText();
        this.setScrolling();
        this.setAutoScroll();
        this.setRestartBtn();




    }

    setText() 
    {
        this.fontLoader.load('/fonts/helvetiker_regular.typeface.json',
        (font) => {
            this.text = new Text();
            this.text.position.x = 0;
            this.text.position.y = texts[textStr].initialPos;
            this.text.position.z = 0;
            this.text.text = texts[textStr].text;
            this.text.fontSize = 1;
            this.text.textAlign = 'justify';
            this.text.anchorX = 'center';
            this.text.anchorY = 'middle';
            this.text.letterSpacing = 0;
            this.text.maxWidth = 30;
            this.text.textIndent = 0;
            this.text.color = 0xffff00;

            this.text.sync();
            this.scene.add(this.text);

            this.trigger('ready');
            
            // Debug
            if(this.debug.active)
            {
                this.debugFolder
                    .add(this.text.position, 'x')
                    .name('textX')
                    .min(- 50)
                    .max(50)
                    .step(0.001)
                
                this.debugFolder
                    .add(this.text.position, 'y')
                    .name('textY')
                    .min(- 50)
                    .max(50)
                    .step(0.001)
                
                this.debugFolder
                    .add(this.text.position, 'z')
                    .name('textZ')
                    .min(- 50)
                    .max(50)
                    .step(0.001)

                    this.debugFolder
                    .add(this.text.rotation, 'x')
                    .name('rotX')
                    .min(- 5)
                    .max(5)
                    .step(0.001)
                
                this.debugFolder
                    .add(this.text.rotation, 'y')
                    .name('rotY')
                    .min(- 5)
                    .max(5)
                    .step(0.001)
                
                this.debugFolder
                    .add(this.text.rotation, 'z')
                    .name('rotZ')
                    .min(- 5)
                    .max(5)
                    .step(0.001)

                    this.debugFolder
                    .add(this, 'speed')
                    .name('scrolling speed')
                    .min(- 5)
                    .max(5)
                    .step(0.001)
            }



        });

    }
    
    setScrolling() 
    {
        document.body.addEventListener('wheel', (e)=> {
            if(!this.experience.started) return;
            if(e.deltaY > 0 )
            {
                this.text.position.y +=this.scrollSens;
            } else 
            {
                this.text.position.y -=this.scrollSens;
            }
            this.text.sync();
            this.scrollSliderOff();
        });
    }

    setAutoScroll()
    {

        this.autoScrollSlider.addEventListener('click', (e) => 
        {
            if(this.autoScrollSlider.checked)
            {
                this.autoScroll = true;
            } else 
            {
                this.autoScroll = false;
            }
        })
    }

    scrollSliderOff()
    {
        this.autoScroll = false;
        this.autoScrollSlider.checked = false;
    }

    scrollSliderOn()
    {
        this.autoScroll = true;
        this.autoScrollSlider.checked = true;
    }

    setRestartBtn()
    {
        this.btnRestart.addEventListener('click', (e)=> {
            this.btnRestart.style.display = "none";
            this.isRunning = true;
            this.text.position.y = texts[textStr].initialPos;
            this.scrollSliderOn();
            this.trigger('restart');
        });
    }

    enableRestartBtn()
    {
        this.btnRestart.style.display = "block";  
    }

    disableRestartBtn()
    {
        this.btnRestart.style.display = "none";  
    }

    update(delta) 
    {   
        if(this.text.position.y >= texts[textStr].finalPos && this.isRunning) 
        {
            this.isRunning = false;
            this.enableRestartBtn();
        }
        else if(this.text.position.y <= texts[textStr].finalPos && !this.isRunning) 
        {
            this.isRunning = true;
            this.disableRestartBtn(); 
        }
        else if(this.autoScroll) 
        {
            this.text.position.y +=this.speed * delta;
            this.text.sync()
        }
        
    }
}