import Experience from '../Experience.js'
import Environment from './Environment.js'
import Text from './text.js'
import Background from './background.js'
import { texts } from '../../../static/texts/Texts.js'
import { textStr } from '../../script.js'
import SoundManager from '../Utils/SoundManager.js'

export default class World
{
    constructor()
    {
        this.experience = new Experience()
        this.scene = this.experience.scene
        this.resources = this.experience.resources
        this.soundManager = this.experience.soundManager

        this.introDiv = document.getElementById('intro-div');
        this.loadingDiv = document.querySelector('.lds-dual-ring');
        this.menuDiv = document.querySelector('#working-menu');
        this.startButton = document.querySelector('.start-btn');
        this.btnMenu = document.querySelector('.btn-menu');
        
        this.welcomeTxt = document.querySelector('#welcome-text');
        this.warningTxt = document.querySelector('#warning-text');

        this.startButton.innerHTML = texts[textStr].start;
        this.welcomeTxt.innerHTML = texts[textStr].intro;
        this.warningTxt.innerHTML = texts[textStr].warning;

        this.startButton.addEventListener("click", ()=> {
            this.start()
        });


        // Wait for resources
        this.resources.on('ready', () =>
        {
            this.text = new Text()
            this.environment = new Environment()
            this.background = new Background()
            this.soundManager = new SoundManager()
            this.ressourcesReady = 0;

            this.soundManager.on('ready', ()=>
            {
                this.ressourcesReady++;
                if(this.ressourcesReady == 2) this.setup();
            })

            this.text.on('ready', ()=> 
            {
                this.ressourcesReady++;
                if(this.ressourcesReady == 2) this.setup();

            })

            this.text.on('restart', () => {
                this.restart();
            });

        })
    }

    start()
    {
        this.menuDiv.style.display = "none";
        this.experience.started = true;
        this.btnMenu.style.display = "block";
        this.soundManager.reset();
        this.text.scrollSliderOn();
        this.soundManager.startAudio();
    }

    restart()
    {
        this.soundManager.restartAudio();
    }

    update(delta)
    {
        if(this.experience.started && this.text) {
            try {
                this.text.update(delta)
            } catch {
                
                }
        }
    }

    setup() 
    {
        this.introDiv.style.display = "none";
        this.menuDiv.style.display = "block";
    }
}