import EventEmitter from './EventEmitter.js'

export default class SoundManager extends EventEmitter
{
    constructor()
    {
        super()

        this.btn = document.querySelector('#soundIcon'); 
        this.slider = document.querySelector('#audio-slider');
        this.audio = new Audio('/audio/introMusic.mp3');
        this.audio.volume = 0.3;

        this.audio.oncanplay = ()=> {
            this.trigger('ready');
        };

        this.btn.addEventListener('click', ()=>
        {
            this.handleClick();
        })

        this.slider.oninput = () => {
            this.setVolume(this.slider.value / 100);
            if(this.slider.value == 0)
            {
                this.btn.checked = true;
            } else
            {
                this.btn.checked = false;
            }
        }
    }

    handleClick()
    {
        if(this.btn.checked) {
            this.setVolume(0);
        } else {
            this.setVolume(this.slider.value/100);
        }
    }

    startAudio()
    {
        this.audio.play();
    }

    restartAudio()
    {
        this.audio.pause();
        this.audio.currentTime = 0;
        this.audio.play();
    }

    setVolume(vol) 
    {
        this.audio.volume = vol; 
    }

    reset()
    {
        this.slider.value = 30;
        this.btn.checked = false;
    }
}